'use client';

import { useState } from 'react';
import { Review, ReviewSummary, reviewsData, reviewSummaries, getReviewsByProduct, getReviewSummary, getStarRating, formatDate } from '@/data/reviews';
import { ShareMenu, shareUtils } from './ShareMenu';

interface ReviewsSectionProps {
  productId?: string;
  showAll?: boolean;
}

interface StarRatingProps {
  rating: number;
  size?: 'sm' | 'md' | 'lg';
  showText?: boolean;
}

interface RatingFilterProps {
  ratingFilter: number | null;
  onRatingFilter: (rating: number | null) => void;
  summary: ReviewSummary;
}

const StarRating = ({ rating, size = 'md', showText = false }: StarRatingProps) => {
  const sizeClasses = {
    sm: 'w-3 h-3',
    md: 'w-4 h-4',
    lg: 'w-5 h-5'
  };

  return (
    <div className="flex items-center gap-1">
      <div className="flex">
        {[1, 2, 3, 4, 5].map((star) => (
          <svg
            key={star}
            className={`${sizeClasses[size]} ${
              star <= rating ? 'text-yellow-400 fill-current' : 'text-gray-300'
            }`}
            viewBox="0 0 20 20"
          >
            <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
          </svg>
        ))}
      </div>
      {showText && (
        <span className="text-sm text-gray-600 ml-1">
          {rating.toFixed(1)} out of 5
        </span>
      )}
    </div>
  );
};

const RatingFilter = ({ ratingFilter, onRatingFilter, summary }: RatingFilterProps) => {
  const { ratingDistribution, totalReviews } = summary;

  return (
    <div className="bg-gray-50 p-4 rounded-lg">
      <h3 className="font-semibold text-lg mb-3">Filter by rating</h3>
      <div className="space-y-2">
        <button
          onClick={() => onRatingFilter(null)}
          className={`w-full text-left p-2 rounded ${
            ratingFilter === null ? 'bg-orange-100 text-orange-800' : 'hover:bg-gray-100'
          }`}
        >
          All reviews ({totalReviews})
        </button>
        {[5, 4, 3, 2, 1].map((rating) => {
          const count = ratingDistribution[rating as keyof typeof ratingDistribution];
          const percentage = totalReviews > 0 ? (count / totalReviews) * 100 : 0;
          
          return (
            <button
              key={rating}
              onClick={() => onRatingFilter(rating)}
              className={`w-full text-left p-2 rounded flex items-center justify-between ${
                ratingFilter === rating ? 'bg-orange-100 text-orange-800' : 'hover:bg-gray-100'
              }`}
            >
              <div className="flex items-center gap-2">
                <StarRating rating={rating} size="sm" />
                <span>({count})</span>
              </div>
              <div className="flex items-center gap-2">
                <div className="w-16 h-2 bg-gray-200 rounded-full">
                  <div
                    className="h-2 bg-yellow-400 rounded-full"
                    style={{ width: `${percentage}%` }}
                  />
                </div>
                <span className="text-xs text-gray-500">{percentage.toFixed(0)}%</span>
              </div>
            </button>
          );
        })}
      </div>
    </div>
  );
};

const ReviewItem = ({ review }: { review: Review }) => {
  const [isShareMenuOpen, setIsShareMenuOpen] = useState(false);
  const [isHelpful, setIsHelpful] = useState(false);
  const [helpfulCount, setHelpfulCount] = useState(review.helpful);

  const handleShare = (platform: string) => {
    const title = `Review: ${review.title}`;
    const text = `Check out this review of ${review.title} by ${review.userName} on Raaga Foods`;
    const url = `${window.location.origin}#review-${review.id}`;
    
    shareUtils.handleShare(platform, title, text, url, () => setIsShareMenuOpen(false));
  };

  const handleHelpful = () => {
    if (!isHelpful) {
      setIsHelpful(true);
      setHelpfulCount(prev => prev + 1);
    }
  };

  return (
    <div id={`review-${review.id}`} className="border-b border-gray-200 py-6">
      <div className="flex items-start gap-4">
        {/* User Avatar */}
        <div className="flex-shrink-0">
          <div className="w-10 h-10 bg-gray-300 rounded-full flex items-center justify-center">
            {review.userAvatar ? (
              <img src={review.userAvatar} alt={review.userName} className="w-10 h-10 rounded-full" />
            ) : (
              <span className="text-sm font-semibold text-gray-600">
                {review.userName.charAt(0)}
              </span>
            )}
          </div>
        </div>

        {/* Review Content */}
        <div className="flex-1">
          {/* User Info */}
          <div className="flex items-center gap-2 mb-2">
            <span className="font-semibold text-gray-900">{review.userName}</span>
            {review.verified && (
              <span className="bg-orange-100 text-orange-800 text-xs px-2 py-1 rounded-full">
                Verified Purchase
              </span>
            )}
            {review.vine && (
              <span className="bg-green-100 text-green-800 text-xs px-2 py-1 rounded-full">
                Vine Customer
              </span>
            )}
            {review.earlyReviewer && (
              <span className="bg-blue-100 text-blue-800 text-xs px-2 py-1 rounded-full">
                Early Reviewer
              </span>
            )}
          </div>

          {/* Rating and Date */}
          <div className="flex items-center gap-4 mb-2">
            <StarRating rating={review.rating} />
            <span className="text-sm text-gray-600">{formatDate(review.date)}</span>
            {review.location && (
              <span className="text-sm text-gray-500">📍 {review.location}</span>
            )}
          </div>

          {/* Review Title */}
          <h4 className="font-semibold text-gray-900 mb-2">{review.title}</h4>

          {/* Review Content */}
          <p className="text-gray-700 mb-4 leading-relaxed">{review.content}</p>

          {/* Review Images */}
          {review.images && review.images.length > 0 && (
            <div className="flex gap-2 mb-4">
              {review.images.map((image, index) => (
                <div key={index} className="w-20 h-20 bg-gray-200 rounded border overflow-hidden">
                  <img 
                    src={image} 
                    alt={`Review image ${index + 1}`}
                    className="w-full h-full object-cover"
                    onError={(e) => {
                      e.currentTarget.style.display = 'none';
                    }}
                  />
                </div>
              ))}
            </div>
          )}

          {/* Actions */}
          <div className="flex items-center gap-4">
            <button
              onClick={handleHelpful}
              className={`flex items-center gap-1 text-sm ${
                isHelpful 
                  ? 'text-orange-600 font-semibold' 
                  : 'text-gray-600 hover:text-orange-600'
              } transition-colors`}
            >
              <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M14 10h4.764a2 2 0 011.789 2.894l-3.5 7A2 2 0 0115.263 21h-4.017c-.163 0-.326-.02-.485-.06L7 20m7-10V5a2 2 0 00-2-2h-.095c-.5 0-.905.405-.905.905 0 .714-.211 1.412-.608 2.006L7 11v9m7-10h-2M7 20H5a2 2 0 01-2-2v-6a2 2 0 012-2h2.5" />
              </svg>
              Helpful ({helpfulCount})
            </button>

            <button className="text-sm text-gray-600 hover:text-orange-600 transition-colors">
              Report
            </button>

            <ShareMenu
              isOpen={isShareMenuOpen}
              onShare={handleShare}
              onToggle={() => setIsShareMenuOpen(!isShareMenuOpen)}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

const ReviewsSection = ({ productId, showAll = false }: ReviewsSectionProps) => {
  const [sortBy, setSortBy] = useState<'newest' | 'oldest' | 'helpful' | 'rating'>('newest');
  const [ratingFilter, setRatingFilter] = useState<number | null>(null);

  // Get reviews data
  const allReviews = productId ? getReviewsByProduct(productId) : reviewsData;
  const summary = productId ? getReviewSummary(productId) : {
    productId: 'all',
    averageRating: 4.5,
    totalReviews: allReviews.length,
    ratingDistribution: { 5: 120, 4: 45, 3: 20, 2: 8, 1: 5 },
    verifiedPurchases: allReviews.filter(r => r.verified).length,
    featuredReviews: allReviews.slice(0, 2).map(r => r.id)
  };

  // Filter and sort reviews
  let filteredReviews = allReviews;
  
  if (ratingFilter) {
    filteredReviews = filteredReviews.filter(review => review.rating === ratingFilter);
  }

  // Sort reviews
  filteredReviews = [...filteredReviews].sort((a, b) => {
    switch (sortBy) {
      case 'newest':
        return new Date(b.date).getTime() - new Date(a.date).getTime();
      case 'oldest':
        return new Date(a.date).getTime() - new Date(b.date).getTime();
      case 'helpful':
        return b.helpful - a.helpful;
      case 'rating':
        return b.rating - a.rating;
      default:
        return 0;
    }
  });

  // Limit reviews if not showing all
  if (!showAll) {
    filteredReviews = filteredReviews.slice(0, 5);
  }

  if (!summary) return null;

  return (
    <div className="bg-white">
      {/* Reviews Header */}
      <div className="border-b border-gray-200 pb-6 mb-6">
        <h2 className="text-2xl font-bold text-gray-900 mb-4">Customer Reviews</h2>
        
        {/* Rating Summary */}
        <div className="flex items-start gap-8">
          <div className="text-center">
            <div className="text-4xl font-bold text-gray-900 mb-1">
              {summary.averageRating.toFixed(1)}
            </div>
            <StarRating rating={Math.round(summary.averageRating)} size="lg" />
            <div className="text-sm text-gray-600 mt-1">
              {summary.totalReviews} reviews
            </div>
          </div>

          <div className="flex-1">
            <div className="space-y-2">
              {[5, 4, 3, 2, 1].map((rating) => {
                const count = summary.ratingDistribution[rating as keyof typeof summary.ratingDistribution];
                const percentage = summary.totalReviews > 0 ? (count / summary.totalReviews) * 100 : 0;
                
                return (
                  <div key={rating} className="flex items-center gap-2">
                    <span className="text-sm text-orange-600 font-medium w-8">
                      {rating} ★
                    </span>
                    <div className="flex-1 h-3 bg-gray-200 rounded-full">
                      <div
                        className="h-3 bg-orange-400 rounded-full"
                        style={{ width: `${percentage}%` }}
                      />
                    </div>
                    <span className="text-sm text-gray-600 w-12 text-right">
                      {percentage.toFixed(0)}%
                    </span>
                  </div>
                );
              })}
            </div>
            <div className="text-sm text-gray-600 mt-3">
              {summary.verifiedPurchases} of {summary.totalReviews} reviews are from verified purchases
            </div>
          </div>
        </div>
      </div>

      {/* Controls */}
      <div className="flex flex-col lg:flex-row gap-6">
        {/* Filters */}
        <div className="lg:w-1/4">
          <RatingFilter
            ratingFilter={ratingFilter}
            onRatingFilter={setRatingFilter}
            summary={summary}
          />
        </div>

        {/* Reviews List */}
        <div className="lg:w-3/4">
          {/* Sort Controls */}
          <div className="flex items-center justify-between mb-6">
            <div className="text-sm text-gray-600">
              Showing {filteredReviews.length} of {summary.totalReviews} reviews
            </div>
            <select
              value={sortBy}
              onChange={(e) => setSortBy(e.target.value as any)}
              className="border border-gray-300 rounded-lg px-3 py-2 text-sm"
            >
              <option value="newest">Newest first</option>
              <option value="oldest">Oldest first</option>
              <option value="helpful">Most helpful</option>
              <option value="rating">Highest rating</option>
            </select>
          </div>

          {/* Reviews */}
          <div className="space-y-6">
            {filteredReviews.length > 0 ? (
              filteredReviews.map((review) => (
                <ReviewItem key={review.id} review={review} />
              ))
            ) : (
              <div className="text-center py-12 text-gray-500">
                No reviews match your filter criteria.
              </div>
            )}
          </div>

          {/* Load More */}
          {!showAll && allReviews.length > 5 && (
            <div className="text-center mt-8">
              <button className="bg-orange-500 text-white px-6 py-3 rounded-lg hover:bg-orange-600 transition-colors">
                See all {summary.totalReviews} reviews
              </button>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default ReviewsSection;
