export interface Review {
  id: string;
  productId: string;
  userId: string;
  userName: string;
  userAvatar?: string;
  rating: number; // 1-5 stars
  title: string;
  content: string;
  date: string;
  verified: boolean; // Verified purchase
  helpful: number; // Number of helpful votes
  images?: string[]; // Review images
  location?: string; // User location
  vine?: boolean; // Vine customer
  earlyReviewer?: boolean;
}

export interface ReviewSummary {
  productId: string;
  averageRating: number;
  totalReviews: number;
  ratingDistribution: {
    5: number;
    4: number;
    3: number;
    2: number;
    1: number;
  };
  verifiedPurchases: number;
  featuredReviews: string[]; // Review IDs
}

export interface PromoCode {
  id: string;
  code: string;
  title: string;
  description: string;
  discount: string;
  category?: string;
  validUntil: string;
  minPurchase?: string;
  maxUses?: number;
  currentUses?: number;
  terms?: string[];
}

