// Helper functions
export const getReviewsByProduct = (productId: string): Review[] => {
  return reviewsData.filter(review => review.productId === productId);
};

export const getReviewSummary = (productId: string): ReviewSummary | undefined => {
  return reviewSummaries.find(summary => summary.productId === productId);
};

export const getStarRating = (rating: number): string => {
  return "★".repeat(rating) + "☆".repeat(5 - rating);
};

export const formatDate = (dateString: string): string => {
  const date = new Date(dateString);
  return date.toLocaleDateString('en-IN', { 
    year: 'numeric', 
    month: 'long', 
    day: 'numeric' 
  });
};

export const getActivePromoCodes = (): PromoCode[] => {
  const now = new Date();
  return promoCodesData.filter(promo => {
    const validUntil = new Date(promo.validUntil);
    return validUntil > now && (!promo.maxUses || (promo.currentUses || 0) < promo.maxUses);
  });
};
